.GoogleAdd{
    display: flex;
    width: var(--wi);
    height: 536px;  
    margin-top: 8%;
}
.main__info{
    width: 70%;
    font-family: var(--FontMain);
}
.title__text{
    width: 80%;
    font-size: calc(71px);
}
.main__text{
    margin-top: 7%;
    width: 55%;
    font-size: 20px;
}
.Btn__getstarted , .Btn__View {
    font-family: var(--FontMain);
    width: 156px;
    height: 38px;
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
}
.btns__main{
    margin-left: 1%;
    margin-top: 4%;
    width: 50%;
    display: flex;
    justify-content: space-between;
}
.Btn__getstarted{
    background: #4CFDC7;
}
.Btn__View{
    width: 220px;
    border: 2px solid black
}
.Google__rate{
    margin-top: 7px;
    margin-left: 7px;
}
.Phone{
    width: 293px;
    height: 536px;
    opacity: 0;
    transform: translateX(120%);
    animation: lt 1.5s forwards;
}
@keyframes lt{
    0%{opacity: 0; transform: translateX(120%);}
    100%{opacity:1;transform: translate(0);}
}


@media (max-width: 1150px) {
    .title__text {
        font-size: 55px;
        width: auto;
        max-width: 560px;
    }
    .main__text {
        max-width: 411px;
        width: auto;
    }
    .btns__main {
        max-width: 390px;
        width: auto;
    }
}

@media (max-width: 768px) {
    .Phone {
        display: none;
    }
    .main__info {
        width: 100%;
    }
}

@media (max-width: 550px) {
    .title__text {
        font-size: 40px;
    }
}

@media (max-width: 420px) {
    .title__text {
        font-size: 30px;
    }
    .main__text {
        font-size: 16px;
    }
    .btns__main {
        display: block;
        a + a {
            margin-top: 15px;
        }
    }
}
